:root {
  --primary-bg: #121212;
  --secondary-bg: #3D3D3D;
  --light-gray: #A6A6A6;
  --white: #F5F5F5;
  --font-main: 'Fira Code', monospace;
  --font-secondary: 'Open Sans', sans-serif;
}

body {
  margin: 0;
  font-family: var(--font-secondary);
  background-color: var(--primary-bg);
  color: var(--white);
  line-height: 1.6;
}

.App {
  max-width: 960px;
  margin: 2rem auto;
  padding: 0 1rem;
}

h1 {
  font-family: var(--font-main);
  font-size: 3rem;
  text-align: center;
  margin-bottom: 2rem;
  color: var(--white);
}

/* SearchBar styling */
.SearchBar {
  display: flex;
  justify-content: center;
  margin-bottom: 2rem;
}

.SearchBar input {
  width: 60%;
  padding: 0.75rem 1rem;
  border: none;
  border-radius: 6px 0 0 6px;
  font-size: 1rem;
  background-color: var(--secondary-bg);
  color: var(--white);
  font-family: var(--font-secondary);
}

.SearchBar input::placeholder {
  color: var(--light-gray);
}

.SearchBar button {
  padding: 0.75rem 1.5rem;
  border: none;
  border-radius: 0 6px 6px 0;
  background-color: var(--white);
  color: var(--primary-bg);
  font-weight: bold;
  cursor: pointer;
  font-family: var(--font-main);
  transition: background-color 0.3s ease;
}

.SearchBar button:hover {
  background-color: var(--light-gray);
}

/* Playlist and TrackList container */
.App-playlist {
  display: flex;
  gap: 1rem;
  flex-wrap: wrap;
  justify-content: center;
}

/* TrackList and Playlist common styles */
.TrackList, .Playlist {
  background-color: var(--secondary-bg);
  border-radius: 8px;
  padding: 1rem;
  flex: 1 1 300px;
  max-width: 450px;
  color: var(--white);
  font-family: var(--font-secondary);
}

/* Playlist name input */
.Playlist input.playlist-name-input {
  width: 100%;
  padding: 0.5rem;
  margin-bottom: 1rem;
  font-family: var(--font-main);
  font-size: 1.2rem;
  border: none;
  border-radius: 6px;
  background-color: var(--primary-bg);
  color: var(--white);
}

/* Track item styling */
.Track {
  display: flex;
  justify-content: space-between;
  padding: 0.5rem 0;
  border-bottom: 1px solid var(--light-gray);
}

.Track:last-child {
  border-bottom: none;
}

.Track-details {
  flex-grow: 1;
  font-size: 0.95rem;
}

.Track-details span {
  display: block;
  color: var(--light-gray);
}

.Track button {
  background-color: transparent;
  border: 2px solid var(--white);
  color: var(--white);
  font-family: var(--font-main);
  font-weight: bold;
  padding: 0.25rem 0.75rem;
  border-radius: 6px;
  cursor: pointer;
  transition: background-color 0.3s ease, color 0.3s ease;
}

.Track button:hover {
  background-color: var(--white);
  color: var(--primary-bg);
}

/* Save Playlist button */
.Playlist button.save-playlist {
  margin-top: 1rem;
  width: 100%;
  padding: 0.75rem;
  background-color: var(--white);
  color: var(--primary-bg);
  font-family: var(--font-main);
  font-weight: bold;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.Playlist button.save-playlist:hover {
  background-color: var(--light-gray);
  color: var(--primary-bg);
}
